import 'nodelist-foreach-polyfill'; // ie11

const ratio_height = (context, query, ratio) => {
  let el = document.querySelector(query)
  if (!el) return
  let height = (el.offsetWidth * ratio).toFixed()

  let styles = {
    'height': height + 'px',
  }
  
  context.ratio_styles = styles
}

const ratio_styles = {}

export {ratio_height, ratio_styles}


